* {
  box-sizing: border-box;
}

body {
  background: #f4f5f7;
  margin: 0;
}

.landing-page-container {
  width: 100%;
  height: 100vh;
}

.App--photographer {
  
}

.App--description {
  background: white;
  width: 100%;
  padding: 20px;
  margin-top: -100px;
  z-index: 50;
  position: relative;
}

.App--loading {
  text-align: center;
  font-weight: bold;
  font-size: 18px;
}

.App--searchbox {
  margin-top: 10px;
  padding: 8px 16px;
  font-size: 14px;
  width: 100%;
  outline: none;
  border: 2px solid #28aae1;
  text-transform: uppercase;
  font-weight: bold;
  color: #253764;
}

.App--body {
  padding: 20px;
}

.EventGrid { 
  display: -webkit-flex; 
  display: flex;
  -webkit-flex-direction: row;
          flex-direction: row;
  -webkit-align-items: flex-start;
          align-items: flex-start;
  -webkit-justify-content: flex-start;
          justify-content: flex-start;
  -webkit-flex-wrap: wrap;
          flex-wrap: wrap;
}

.EventGrid--divider {
  min-width: 20px;
  min-height: 20px;
}

.EventGrid--column {
  -webkit-flex: 1 1;
          flex: 1 1;
}

.Event {
  margin-top: 20px;
  background-color: white;
  color: black;
}

.Event-title {
  color: #253764;
  text-transform: uppercase;
  letter-spacing: .05em;
  word-break: break-word;
  -webkit-hyphens: auto;
      -ms-hyphens: auto;
          hyphens: auto;
}

.Event-host {
  color: #253764;
  font-weight: bold;
  font-size: 22px;
}

.Event-expand {
  color: #28aae1;
  text-decoration: underline;
  cursor: pointer;
}

.Event-description {
  white-space: pre-wrap;
}

.Event-tag {
  background-color: #28aae1;
  color: white;
  margin-right: 10px;
  margin-top: 20px;
  margin-bottom: 20px;
  padding: 5px;
  text-transform: uppercase;
  letter-spacing: .05em;
}

.Event-tags {
  display: -webkit-flex;
  display: flex;
  -webkit-flex-direction: row;
          flex-direction: row;
  -webkit-flex-wrap: wrap;
          flex-wrap: wrap;
  margin-top: 10px;
}

.Event-color {
  height: 20px;
}
.Event-contents {
  padding: 20px;
}
.Tag {
	margin-right: 10px;
	margin-top: 10px;
	padding: 8px 16px;
	background: #28aae1;
	color: white;
	outline: none;
	cursor: pointer;
	display: inline-block;
	transition: all 0.1s ease;
}

.Tag--text {
	font-weight: bold;
	text-transform: uppercase;
	letter-spacing: .05em;
	font-size: 14px;
}

.Tag-selected {
	background: #253764;
}

.EventColumn {
	-webkit-flex: 1 1;
	        flex: 1 1;
	display: -webkit-flex;
	display: flex;
	-webkit-flex-direction: column;
	        flex-direction: column;
}

.EventColumn--title {
	background: #253764;
	color: white;
	padding: 20px;
	margin: 0;
	margin-bottom: 20px;
}

.EventColumn--title span {
	font-size: 18px;
	letter-spacing: 0;
}

.EventColumn--empty {
	height: 300px;
	padding-bottom: 100px;
	-webkit-flex: 1 1;
	        flex: 1 1;
	display: -webkit-flex;
	display: flex;
	-webkit-justify-content: center;
	        justify-content: center;
	-webkit-align-items: center;
	        align-items: center;
}

.EventColumn--empty__text {
	font-size: 16px;
	font-weight: bold;
}
.Filters {

}

.Filters--title {

}
.HeroImage {
	width: 100%;
	height: 600px;
	background: red;
	position: relative;
	display: -webkit-flex;
	display: flex;
	-webkit-justify-content: center;
	        justify-content: center;
	-webkit-align-items: center;
	        align-items: center;
	padding: 30px;
	background: #253764;
}

.HeroImage--img {
	position: absolute;
	top: 0;
	left: 0;
	width: 100%;
	height: 100%;
	object-fit: cover;
	opacity: 0.5;
}

.HeroImage--title {
	position: relative;
	z-index: 10;
	color: white;
	text-transform: uppercase;
	font-size: 40px;
}

.App--photographer {
	position: absolute;
	top: 2px;
	right: 20px;
	color: white;
	opacity: 0.5;
}

@font-face {
  font-family: 'Tondu';
  src: url(/Tyopajatyokalu/static/media/Tondu_Beta.ef566c49.ttf);
  font-weight: normal;
  font-style: normal;
}

@font-face {
  font-family: 'SourceSansPro';
  src: url(/Tyopajatyokalu/static/media/SourceSansPro-Regular.3f482321.ttf);
  font-weight: normal;
  font-style: normal;
}

@font-face {
  font-family: 'SourceSansPro';
  src: url(/Tyopajatyokalu/static/media/SourceSansPro-Bold.7e0ee8c4.ttf);
  font-weight: bold;
  font-style: normal;
}

* {
	font-family: 'SourceSansPro';
	font-weight: normal;
}

h1,
h2,
h3,
h4,
h5,
h6 {
	font-family: 'Tondu';
	text-transform: uppercase;
	letter-spacing: 0.05em;
}
